import React, { Component } from "react";
import { Card, Row, Col, Badge, Modal, Container } from "react-bootstrap";
import { NavLink } from "react-router-dom";
import {
  FacebookShareButton,
  FacebookIcon,
  TwitterShareButton,
  TwitterIcon,
  EmailShareButton,
  EmailIcon,
} from "react-share";
import TextTruncate from "react-text-truncate";
import { MdShare, MdDelete } from "react-icons/md";
import defaultGuardianImg from "../images/guardiandefault.png";
import defaultNytimesImg from "../images/nytimesdefault.jpg";

class SmallArticleCard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showShareModal: false,
    };
    this.closeModal = this.closeModal.bind(this);
    this.openModal = this.openModal.bind(this);
    this.defaultImage = this.defaultImage.bind(this);
    this.getSection = this.getSection.bind(this);
  }

  closeModal() {
    this.setState((state) => ({ showShareModal: !state.showShareModal }));
  }

  openModal(e) {
    e.preventDefault();
    e.stopPropagation();
    e.nativeEvent.stopImmediatePropagation();
    this.setState((state) => ({ showShareModal: !state.showShareModal }));
  }

  defaultImage(source) {
    if (source === "nytimes") return defaultNytimesImg;
    else if (source === "guardian") return defaultGuardianImg;
  }

  getSection(section) {
    switch (section) {
      case "world":
        return "primary";
      case "politics":
        return "secondary";
      case "business":
        return "success";
      case "technology":
        return "danger";
      case "sport":
        return "warning";
      case "guardian":
        return "light";
      case "nytimes":
        return "dark";
      default:
        return "info";
    }
  }

  render() {
    return (
      <>
        <NavLink
          to={`/article?q=${this.props.article.id}${
            this.props.article.hasOwnProperty("source")
              ? "&source=" + this.props.article.source
              : ""
          }`}
          style={{ textDecoration: "none", color: "inherit", outline: "none" }}
        >
          <Card className="m-2 shadow mb-sm-5 mt-sm-0">
            <Card.Body>
              <Card.Subtitle
                className="font-weight-bold font-italic"
                style={{ lineHeight: 1.5 }}
              >
                <TextTruncate
                  line={2}
                  truncateText="..."
                  element="div"
                  textElement="div"
                  text={this.props.article.title}
                  containerClassName="small-article-card-title-truncate"
                />
                <MdShare
                  style={{ cursor: "pointer" }}
                  onClick={this.openModal}
                />
                {this.props.article.hasOwnProperty("source") ? (
                  <MdDelete
                    style={{ cursor: "pointer" }}
                    onClick={(e) => {
                      this.props.deleteBookmark(this.props.article.id, e);
                    }}
                  />
                ) : null}
              </Card.Subtitle>
              <Card.Img
                src={
                  this.props.article.image === ""
                    ? this.defaultImage(localStorage.getItem("newsSource"))
                    : this.props.article.image
                }
                className="img-thumbnail my-2"
              />
              <Row className="badge-time-row">
                <Col className="mr-auto font-italic font-weight-normal pr-0">
                  {this.props.article.date}
                </Col>
                <Col
                  className={
                    this.props.article.hasOwnProperty("source")
                      ? "d-flex justify-content-end"
                      : "text-right"
                  }
                >
                  <Badge variant={this.getSection(this.props.article.section)}>
                    {this.props.article.section.toUpperCase()}
                  </Badge>
                  {this.props.article.hasOwnProperty("source") ? (
                    <Badge
                      variant={this.getSection(this.props.article.source)}
                      className="ml-1"
                    >
                      {this.props.article.source.toUpperCase()}
                    </Badge>
                  ) : null}
                </Col>
              </Row>
            </Card.Body>
          </Card>
        </NavLink>

        <Modal show={this.state.showShareModal} onHide={this.closeModal}>
          <Modal.Header closeButton>
            <Row>
              {this.props.article.hasOwnProperty("source") ? (
                <Modal.Title className="col-sm-12 col-12">
                  {this.props.article.source.toUpperCase()}
                </Modal.Title>
              ) : null}
              <Modal.Title
                className="h5 col-sm-12 col-12"
                style={{ lineHeight: 1.1, wordBreak: "break-all" }}
              >
                {this.props.article.title}
              </Modal.Title>
            </Row>
          </Modal.Header>
          <Modal.Body>
            <Container>
              <Row className="justify-content-center h5">Share Via</Row>
              <Row className="justify-content-around">
                <FacebookShareButton
                  url={this.props.article.url}
                  quote={"#CSCI_571_NewsApp"}
                >
                  <FacebookIcon size={60} round />
                </FacebookShareButton>
                <TwitterShareButton
                  url={this.props.article.url}
                  hashtags={["CSCI_571_NewsApp"]}
                >
                  <TwitterIcon size={60} round />
                </TwitterShareButton>
                <EmailShareButton
                  url={this.props.article.url}
                  subject={"#CSCI_571_NewsApp"}
                >
                  <EmailIcon size={60} round />
                </EmailShareButton>
              </Row>
            </Container>
          </Modal.Body>
        </Modal>
      </>
    );
  }
}

export default SmallArticleCard;
