import React, { Component } from "react";
import Article from "./Article.js";
import Loading from "./Loading.js";
import { Container, Row, Col } from "react-bootstrap";

class TabPages extends Component {
  constructor(props) {
    super(props);
    this.state = {
      news: [],
      loading: true,
      newsSource: "guardian",
    };
  }

  componentDidMount() {
    if (localStorage.getItem("newsSource") !== null) {
      this.setState({ newsSource: localStorage.getItem("newsSource") }, () => {
        fetch(
          `/newsapi/v1.0/${this.state.newsSource}${
            this.props.tab === "/sport"
              ? this.state.newsSource === "nytimes"
                ? "/sports"
                : "/sport"
              : this.props.tab
          }`
        )
          .then((res) => res.json())
          .then((data) => this.setState({ news: data, loading: false }));
      });
      this.props.displaySwitchOrNot(this.props.location.pathname);
    }
  }

  componentDidUpdate(prevProps) {
    if (this.props.tab !== prevProps.tab) {
      this.setState({ loading: true });
      fetch(
        `/newsapi/v1.0/${this.state.newsSource}${
          this.props.tab === "/sport"
            ? this.state.newsSource === "nytimes"
              ? "/sports"
              : "/sport"
            : this.props.tab
        }`
      )
        .then((res) => res.json())
        .then((data) => this.setState({ news: data, loading: false }));
    }
  }

  render() {
    return (
      <>
        {this.state.loading ? (
          <Loading />
        ) : (
          <Container fluid>
            {this.state.news.map((article, idx) => {
              return (
                <Row key={idx}>
                  <Col>
                    <Article article={article} />
                  </Col>
                </Row>
              );
            })}
          </Container>
        )}
      </>
    );
  }
}

export default TabPages;
