import React, { Component } from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect
} from "react-router-dom";
import Navigation from "./components/NavigationBar.js";
import TabPages from "./components/TabPages.js";
import ArticleDetail from "./components/ArticleDetail.js";
import Favorites from "./components/Favorites.js";
import SearchResults from "./components/SearchResults";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";

class App extends Component {
  constructor() {
    super();
    this.state = {
      showSwitch: true
    };
    this.checkPathname = this.checkPathname.bind(this);
    this.displaySwitchOrNot = this.displaySwitchOrNot.bind(this);
  }

  displaySwitchOrNot(pathname) {
    this.setState({
      showSwitch: this.checkPathname(pathname)
    });
  }

  checkPathname(pathname) {
    return pathname.includes("search") ||
      pathname.includes("favorites") ||
      pathname.includes("article")
      ? false
      : true;
  }

  render() {
    return (
      <Router>
        <div className="App">
          <Navigation showSwitch={this.state.showSwitch} />
          <Switch>
            <Route
              path="/home"
              render={props => {
                return (
                  <TabPages
                    {...props}
                    tab={""}
                    displaySwitchOrNot={this.displaySwitchOrNot}
                  />
                );
              }}
            />
            <Route
              path="/world"
              render={props => {
                return (
                  <TabPages
                    {...props}
                    tab={"/world"}
                    displaySwitchOrNot={this.displaySwitchOrNot}
                  />
                );
              }}
            />
            <Route
              path="/politics"
              render={props => {
                return (
                  <TabPages
                    {...props}
                    tab={"/politics"}
                    displaySwitchOrNot={this.displaySwitchOrNot}
                  />
                );
              }}
            />
            <Route
              path="/business"
              render={props => {
                return (
                  <TabPages
                    {...props}
                    tab={"/business"}
                    displaySwitchOrNot={this.displaySwitchOrNot}
                  />
                );
              }}
            />
            <Route
              path="/technology"
              render={props => {
                return (
                  <TabPages
                    {...props}
                    tab={"/technology"}
                    displaySwitchOrNot={this.displaySwitchOrNot}
                  />
                );
              }}
            />
            <Route
              path="/sports"
              render={props => {
                return (
                  <TabPages
                    {...props}
                    tab={"/sport"}
                    displaySwitchOrNot={this.displaySwitchOrNot}
                  />
                );
              }}
            />
            <Route
              path="/article"
              render={props => {
                return (
                  <ArticleDetail
                    {...props}
                    displaySwitchOrNot={this.displaySwitchOrNot}
                  />
                );
              }}
            />
            <Route
              path="/favorites"
              render={props => {
                return (
                  <Favorites
                    {...props}
                    displaySwitchOrNot={this.displaySwitchOrNot}
                  />
                );
              }}
            />
            <Route
              path="/search"
              render={props => {
                return (
                  <SearchResults
                    {...props}
                    displaySwitchOrNot={this.displaySwitchOrNot}
                  />
                );
              }}
            />
            <Redirect to="/home" />
          </Switch>
        </div>
      </Router>
    );
  }
}

export default App;
