import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import TextTruncate from "react-text-truncate";
import {
  Card,
  Badge,
  Row,
  Col,
  Modal,
  Container,
  Image,
} from "react-bootstrap";
import {
  EmailShareButton,
  EmailIcon,
  TwitterShareButton,
  TwitterIcon,
  FacebookShareButton,
  FacebookIcon,
} from "react-share";
import { MdShare } from "react-icons/md";
import defaultGuardianImg from "../images/guardiandefault.png";
import defaultNytimesImg from "../images/nytimesdefault.jpg";

class Article extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showShareModal: false,
    };
    this.showModal = this.showModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
    this.defaultImage = this.defaultImage.bind(this);
  }

  showModal(e) {
    e.preventDefault();
    e.stopPropagation();
    e.nativeEvent.stopImmediatePropagation();
    this.setState((state) => ({ showShareModal: !state.showShareModal }));
  }

  closeModal() {
    this.setState((state) => ({ showShareModal: !state.showShareModal }));
  }

  getSection = (section) => {
    switch (section) {
      case "world":
        return "primary";
      case "politics":
        return "secondary";
      case "business":
        return "success";
      case "technology":
        return "danger";
      case "sport":
        return "warning";
      case "sports":
        return "warning";
      default:
        return "info";
    }
  };

  defaultImage() {
    if (localStorage.getItem("newsSource") === "nytimes")
      return defaultNytimesImg;
    else if (localStorage.getItem("newsSource") === "guardian")
      return defaultGuardianImg;
  }

  render() {
    return (
      <>
        <NavLink
          to={`/article?q=${this.props.article.id}`}
          style={{ textDecoration: "none", color: "inherit", outline: "none" }}
        >
          <Card className="m-2 shadow m-sm-3">
            <Row>
              <Col sm={3} className="mx-4 mx-sm-0">
                <Image
                  className="img-thumbnail img-fluid my-sm-4 ml-sm-4 mt-4 mb-n3"
                  src={
                    this.props.article.image === ""
                      ? this.defaultImage()
                      : this.props.article.image
                  }
                />
              </Col>
              <Col sm={9}>
                <Card.Body>
                  <Card.Title
                    className="font-italic font-weight-bold"
                    style={{ lineHeight: 1.5 }}
                  >
                    {this.props.article.title}
                    <MdShare
                      style={{ cursor: "pointer" }}
                      onClick={this.showModal}
                    />
                  </Card.Title>
                  <TextTruncate
                    line={3}
                    truncateText="..."
                    element="p"
                    text={this.props.article.description}
                  />
                  <Row>
                    <Col className="mr-auto font-italic font-weight-normal">
                      <span>{this.props.article.date}</span>
                    </Col>
                    <Col className="text-right">
                      <Badge
                        variant={this.getSection(this.props.article.section)}
                      >
                        {this.props.article.section.toUpperCase()}
                      </Badge>
                    </Col>
                  </Row>
                </Card.Body>
              </Col>
            </Row>
          </Card>
        </NavLink>

        <Modal show={this.state.showShareModal} onHide={this.closeModal}>
          <Modal.Header closeButton>
            <Modal.Title
              className="h5"
              style={{ lineHeight: 1.1, wordBreak: "break-all" }}
            >
              {this.props.article.title}
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Container>
              <Row className="justify-content-center h5">Share Via</Row>
              <Row className="justify-content-around">
                <FacebookShareButton
                  url={this.props.article.url}
                  quote={"#CSCI_571_NewsApp"}
                >
                  <FacebookIcon size={60} round />
                </FacebookShareButton>
                <TwitterShareButton
                  url={this.props.article.url}
                  hashtags={["CSCI_571_NewsApp"]}
                >
                  <TwitterIcon size={60} round />
                </TwitterShareButton>
                <EmailShareButton
                  url={this.props.article.url}
                  subject={"#CSCI_571_NewsApp"}
                >
                  <EmailIcon size={60} round />
                </EmailShareButton>
              </Row>
            </Container>
          </Modal.Body>
        </Modal>
      </>
    );
  }
}

export default Article;
