import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import AsyncSelect from "react-select/async";

class SearchSelect extends Component {
  constructor(props) {
    super(props);
    this.state = {
      inputValue: null,
    };
    this.getOptions = this.getOptions.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  componentDidUpdate(prevprob) {
    if (
      this.props.location !== prevprob.location &&
      !this.props.location.pathname.includes("search")
    )
      this.setState({ inputValue: null });
  }

  mapSuggestionsToValues = (suggestions) => {
    return suggestions.map((suggestion) => ({
      value: suggestion.displayText,
      label: suggestion.query,
    }));
  };

  handleChange = (selectedOption) => {
    if (selectedOption !== null && selectedOption.value !== "") {
      this.setState({ inputValue: selectedOption });
      this.props.history.push({
        pathname: "/search",
        search: `?q=${selectedOption.value}`,
      });
    }
  };

  getOptions = (inputValue, callback) => {
    if (!inputValue) {
      return callback([]);
    }

    fetch(
      `https://api.cognitive.microsoft.com/bing/v7.0/Suggestions?mkt=en-US&q=${inputValue}`,
      {
        headers: {
          "Ocp-Apim-Subscription-Key": "6bc53ba1cace4e538e0d641921cb52ed",
        },
      }
    )
      .then((res) => res.json())
      .then((data) => {
        const results = data.suggestionGroups[0].searchSuggestions;
        callback(this.mapSuggestionsToValues(results));
      });
  };

  render() {
    return (
      <div style={{ flex: "0 0 16em" }}>
        <AsyncSelect
          value={this.state.inputValue}
          isClearable
          isSearchable
          placeholder="Enter Keyword .."
          loadOptions={this.getOptions}
          onChange={this.handleChange}
          noOptionsMessage={() => "No Match"}
        />
      </div>
    );
  }
}

export default withRouter(SearchSelect);
