import React, { Component } from "react";
import Loading from "./Loading.js";
import { Card, Row, Col } from "react-bootstrap";
import ReactTooltip from "react-tooltip";
import { scroller, Element, animateScroll } from "react-scroll";
import {
  FacebookShareButton,
  FacebookIcon,
  TwitterShareButton,
  TwitterIcon,
  EmailShareButton,
  EmailIcon,
} from "react-share";
import {
  FaBookmark,
  FaRegBookmark,
  FaChevronDown,
  FaChevronUp,
} from "react-icons/fa";
import { ToastContainer, toast, cssTransition } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import CommentBox from "./CommentBox.js";
import defaultGuardianImg from "../images/guardiandefault.png";
import defaultNytimesImg from "../images/nytimesdefault.jpg";

const Zoom = cssTransition({
  enter: "zoomIn",
  exit: "zoomOut",
  duration: 800,
  appendPosition: false,
});

class ArticleDetail extends Component {
  constructor(props) {
    super(props);
    this.state = {
      articleInfo: undefined,
      loading: true,
      showFullDescription: false,
      source: undefined,
      bookmarked: false,
    };
    this.showFullDesc = this.showFullDesc.bind(this);
    this.closeFullDesc = this.closeFullDesc.bind(this);
    this.defaultImage = this.defaultImage.bind(this);
    this.addBookmark = this.addBookmark.bind(this);
    this.deleteBookmark = this.deleteBookmark.bind(this);
  }

  showFullDesc() {
    this.setState(
      {
        showFullDescription: true,
      },
      () => {
        scroller.scrollTo("bottomRef", { smooth: true, duration: 800 });
      }
    );
  }

  closeFullDesc() {
    animateScroll.scrollToTop({ smooth: true, duration: 800 });
    this.setState({
      showFullDescription: false,
    });
  }

  addBookmark() {
    ReactTooltip.hide();
    this.setState({ bookmarked: true }, () => {
      let bookmarkArr =
        JSON.parse(localStorage.getItem("bookmarks")) == null
          ? []
          : JSON.parse(localStorage.getItem("bookmarks"));
      let newFavorite = {};
      newFavorite.id = this.state.articleInfo.id;
      newFavorite.title = this.state.articleInfo.title;
      newFavorite.image = this.state.articleInfo.image;
      newFavorite.url = this.state.articleInfo.url;
      newFavorite.date = this.state.articleInfo.date;
      newFavorite.source = localStorage.getItem("newsSource");
      newFavorite.section = this.state.articleInfo.section;
      bookmarkArr.push(newFavorite);
      localStorage.setItem("bookmarks", JSON.stringify(bookmarkArr));
      toast(`Saving - ${this.state.articleInfo.title}`, {
        transition: Zoom,
        autoClose: 3000,
      });
    });
  }

  deleteBookmark() {
    ReactTooltip.hide();
    this.setState({ bookmarked: false }, () => {
      let bookmarkArr = JSON.parse(localStorage.getItem("bookmarks"));
      bookmarkArr.splice(
        bookmarkArr.findIndex(
          (article) => article.id === this.state.articleInfo.id
        ),
        1
      );
      localStorage.setItem("bookmarks", JSON.stringify(bookmarkArr));
      toast(`Removing - ${this.state.articleInfo.title}`, {
        transition: Zoom,
        autoClose: 3000,
      });
    });
  }

  componentDidMount() {
    this.setState(
      {
        source: localStorage.getItem("newsSource"),
      },
      () => {
        let searchParams = new URLSearchParams(this.props.location.search);
        let source = searchParams.has("source")
          ? searchParams.get("source")
          : this.state.source;
        fetch(`/newsapi/v1.0/${source}/articles/${searchParams.get("q")}`)
          .then((res) => res.json())
          .then((data) =>
            this.setState({ articleInfo: data, loading: false }, () => {
              this.setState({
                bookmarked:
                  localStorage.getItem("bookmarks") === null
                    ? false
                    : JSON.parse(localStorage.getItem("bookmarks")).findIndex(
                        (article) => article.url === this.state.articleInfo.url
                      ) === -1
                    ? false
                    : true,
              });
            })
          );
        this.props.displaySwitchOrNot(this.props.location.pathname);
      }
    );
  }

  componentDidUpdate() {
    ReactTooltip.rebuild();
  }

  defaultImage() {
    if (this.state.source === "nytimes") return defaultNytimesImg;
    else if (this.state.source === "guardian") return defaultGuardianImg;
  }

  render() {
    return (
      <>
        <ToastContainer
          position="top-center"
          autoClose={3000}
          hideProgressBar
          closeOnClick
          draggable
        />
        {this.state.loading ? (
          <Loading />
        ) : (
          <>
            <Card className="m-3 shadow">
              <Card.Body>
                <Card.Title
                  className="font-italic"
                  style={{ fontSize: "2rem" }}
                >
                  {this.state.articleInfo.title}
                </Card.Title>
                <Card.Subtitle>
                  <Row style={{ lineHeight: 1.5 }}>
                    <Col sm={2} xs={5} className="font-italic">
                      {this.state.articleInfo.date}
                    </Col>
                    <Col
                      sm={{ span: 2, offset: 7 }}
                      xs={5}
                      className="text-right"
                    >
                      <FacebookShareButton
                        url={this.state.articleInfo.url}
                        quote={"#CSCI_571_NewsApp"}
                        data-tip
                        data-for="facebook-tooltip"
                      >
                        <FacebookIcon size={30} round />
                      </FacebookShareButton>
                      <ReactTooltip
                        id="facebook-tooltip"
                        place="top"
                        type="dark"
                        effect="solid"
                        className="p-1 font-weight-bold"
                      >
                        <span>Facebook</span>
                      </ReactTooltip>

                      <TwitterShareButton
                        url={this.state.articleInfo.url}
                        hashtags={["CSCI_571_NewsApp"]}
                        data-tip
                        data-for="twitter-tooltip"
                      >
                        <TwitterIcon size={30} round />
                      </TwitterShareButton>
                      <ReactTooltip
                        id="twitter-tooltip"
                        place="top"
                        type="dark"
                        effect="solid"
                        className="p-1 font-weight-bold"
                      >
                        <span>Twitter</span>
                      </ReactTooltip>

                      <EmailShareButton
                        url={this.state.articleInfo.url}
                        subject={"#CSCI_571_NewsApp"}
                        data-tip
                        data-for="email-tooltip"
                      >
                        <EmailIcon size={30} round />
                      </EmailShareButton>
                      <ReactTooltip
                        id="email-tooltip"
                        place="top"
                        type="dark"
                        effect="solid"
                        className="p-1 font-weight-bold"
                      >
                        <span>Email</span>
                      </ReactTooltip>
                    </Col>
                    <Col sm={1} xs={2} className="text-right">
                      {this.state.bookmarked ? (
                        <FaBookmark
                          className="ml-auto"
                          color={"#dd143c"}
                          fontSize={"1.5rem"}
                          cursor={"pointer"}
                          onClick={this.deleteBookmark}
                          data-tip="fa-detail-tooltip"
                          data-for="bookmark-tooltip-detail1"
                        />
                      ) : (
                        <FaRegBookmark
                          className="ml-auto"
                          color={"#dd143c"}
                          fontSize={"1.5rem"}
                          cursor={"pointer"}
                          onClick={this.addBookmark}
                          data-tip="ref-detail-tooltip"
                          data-for="bookmark-tooltip-detail2"
                        />
                      )}
                      <ReactTooltip
                        id="bookmark-tooltip-detail1"
                        place="top"
                        type="dark"
                        effect="solid"
                        className="p-1 font-weight-bold"
                      >
                        <span>Bookmark</span>
                      </ReactTooltip>
                      <ReactTooltip
                        id="bookmark-tooltip-detail2"
                        place="top"
                        type="dark"
                        effect="solid"
                        className="p-1 font-weight-bold"
                      >
                        <span>Bookmark</span>
                      </ReactTooltip>
                    </Col>
                  </Row>
                </Card.Subtitle>
                <Card.Img
                  src={
                    this.state.articleInfo.image === ""
                      ? this.defaultImage()
                      : this.state.articleInfo.image
                  }
                  className="mt-2"
                ></Card.Img>
                <Card.Text className="my-2">
                  {this.state.articleInfo.description === ""
                    ? this.state.articleInfo.description
                    : this.state.articleInfo.description
                        .match(/\S.*?\."?(?=\s|$)/g)
                        .slice(0, 4)
                        .join(" ")}
                </Card.Text>
                {this.state.showFullDescription ? (
                  <>
                    <Element name="bottomRef" className="element"></Element>
                    <Card.Text>
                      {this.state.articleInfo.description === ""
                        ? this.state.articleInfo.description
                        : this.state.articleInfo.description
                            .match(/\S.*?\."?(?=\s|$)/g)
                            .slice(4)
                            .join(" ")}
                    </Card.Text>
                  </>
                ) : null}
                {this.state.articleInfo.description.match(/\S.*?\."?(?=\s|$)/g)
                  .length > 4 ? (
                  this.state.showFullDescription ? (
                    <Row>
                      <Col sm={{ span: 1, offset: 11 }} className="text-right">
                        <FaChevronUp
                          cursor={"pointer"}
                          onClick={this.closeFullDesc}
                        />
                      </Col>
                    </Row>
                  ) : (
                    <Row>
                      <Col sm={{ span: 1, offset: 11 }} className="text-right">
                        <FaChevronDown
                          cursor={"pointer"}
                          onClick={this.showFullDesc}
                        />
                      </Col>
                    </Row>
                  )
                ) : null}
              </Card.Body>
            </Card>
            <CommentBox id={this.state.articleInfo.id} />
          </>
        )}
      </>
    );
  }
}

export default ArticleDetail;
