import React, { Component } from "react";
import Switch from "react-switch";
import SearchSelect from "./SearchSelect.js";
import { Navbar, Nav } from "react-bootstrap";
import ReactTooltip from "react-tooltip";
import { NavLink } from "react-router-dom";
import { FaBookmark, FaRegBookmark } from "react-icons/fa";

class NavigationBar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      checked: true,
    };
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(checked) {
    this.setState({ checked }, () => {
      localStorage.setItem(
        "newsSource",
        this.state.checked ? "guardian" : "nytimes"
      );
      window.location.reload(true);
    });
  }

  componentDidMount() {
    if (localStorage.getItem("newsSource") !== null) {
      let newsSource = localStorage.getItem("newsSource");
      let switchStatus = newsSource === "nytimes" ? false : true;
      this.setState({ checked: switchStatus });
    } else {
      localStorage.setItem(
        "newsSource",
        this.state.checked ? "guardian" : "nytimes"
      );
    }
  }

  componentDidUpdate() {
    ReactTooltip.rebuild();
  }

  render() {
    return (
      <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
        <SearchSelect />
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="mr-auto">
            <Nav.Link as={NavLink} to="/home" activeClassName="active">
              Home
            </Nav.Link>
            <Nav.Link as={NavLink} to="/world" activeClassName="active">
              World
            </Nav.Link>
            <Nav.Link as={NavLink} to="/politics" activeClassName="active">
              Politics
            </Nav.Link>
            <Nav.Link as={NavLink} to="/business" activeClassName="active">
              Business
            </Nav.Link>
            <Nav.Link as={NavLink} to="/technology" activeClassName="active">
              Technology
            </Nav.Link>
            <Nav.Link as={NavLink} to="/sports" activeClassName="active">
              Sports
            </Nav.Link>
          </Nav>
          <Nav style={{ fontSize: 1.25 + "rem" }}>
            <Nav.Link
              as={NavLink}
              to="/favorites"
              onClick={() => {
                ReactTooltip.hide();
              }}
            >
              {window.location.pathname.includes("/favorites") ? (
                <FaBookmark
                  color={"#fff"}
                  data-tip="fa-tip"
                  data-for="bookmark-tooltip-nav1"
                />
              ) : (
                <FaRegBookmark
                  color={"#fff"}
                  data-tip="fa-reg-tip"
                  data-for="bookmark-tooltip-nav2"
                />
              )}
            </Nav.Link>
            <ReactTooltip
              id="bookmark-tooltip-nav1"
              place="bottom"
              type="dark"
              effect="solid"
              className="p-1 font-weight-bold"
            >
              <span>Bookmark</span>
            </ReactTooltip>
            <ReactTooltip
              id="bookmark-tooltip-nav2"
              place="bottom"
              type="dark"
              effect="solid"
              className="p-1 font-weight-bold"
            >
              <span>Bookmark</span>
            </ReactTooltip>
            {this.props.showSwitch ? (
              <>
                <Nav.Link disabled="true">NYTimes</Nav.Link>
                <Nav.Link>
                  <Switch
                    onChange={this.handleChange}
                    checked={this.state.checked}
                    onColor="#4a98e9"
                    offColor="#ddd"
                    uncheckedIcon={false}
                    checkedIcon={false}
                    height={30}
                    width={60}
                    handleDiameter={30}
                  />
                </Nav.Link>
                <Nav.Link disabled="true">Guardian</Nav.Link>
              </>
            ) : null}
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    );
  }
}

export default NavigationBar;
