import React, { Component } from "react";
import { Container, Col, Row } from "react-bootstrap";
import { ToastContainer, toast, cssTransition } from "react-toastify";
import SmallArticleCard from "./SmallArticleCard.js";

const Zoom = cssTransition({
  enter: "zoomIn",
  exit: "zoomOut",
  duration: 750,
  appendPosition: false
});

class Favorites extends Component {
  constructor(props) {
    super(props);
    this.state = {
      bookmarks: []
    };
  }

  deleteBookmark(id, e) {
    e.preventDefault();
    e.stopPropagation();
    e.nativeEvent.stopImmediatePropagation();
    let bookmarkArr = JSON.parse(localStorage.getItem("bookmarks"));
    let index = bookmarkArr.findIndex(article => article.id === id);
    toast(`Removing - ${bookmarkArr[index].title}`, {
      transition: Zoom,
      autoClose: 3000
    });
    bookmarkArr.splice(index, 1);
    localStorage.setItem("bookmarks", JSON.stringify(bookmarkArr));
    this.setState({ bookmarks: JSON.parse(localStorage.getItem("bookmarks")) });
  }

  componentDidMount() {
    this.setState({
      bookmarks:
        JSON.parse(localStorage.getItem("bookmarks")) === null
          ? []
          : JSON.parse(localStorage.getItem("bookmarks"))
    });
    this.props.displaySwitchOrNot(this.props.location.pathname);
  }

  render() {
    return (
      <>
        <ToastContainer
          position="top-center"
          autoClose={3000}
          hideProgressBar
          closeOnClick
          draggable
        />
        <Container fluid>
          {this.state.bookmarks.length === 0 ? (
            <Row className="h3 mt-2 justify-content-center">
              You have no saved articles
            </Row>
          ) : (
            <Row className="h3 pl-3 mt-2">Favorites</Row>
          )}
          <Row>
            {this.state.bookmarks.map((article, idx) => {
              return (
                <Col key={idx} sm={3}>
                  <SmallArticleCard
                    article={article}
                    deleteBookmark={this.deleteBookmark.bind(this)}
                  />
                </Col>
              );
            })}
          </Row>
        </Container>
      </>
    );
  }
}

export default Favorites;
