import React, { Component } from "react";
import { Container, Row, Col } from "react-bootstrap";
import Loading from "./Loading.js";
import SmallArticalCard from "./SmallArticleCard.js";

class SearchResults extends Component {
  constructor(props) {
    super(props);
    this.state = {
      source: undefined,
      resultsList: [],
      loading: true
    };
  }

  componentDidMount() {
    this.setState(
      {
        source: localStorage.getItem("newsSource")
      },
      () => {
        fetch(
          `/newsapi/v1.0/${
            this.state.source
          }/search/${this.props.location.search.substring(3)}`
        )
          .then(res => res.json())
          .then(data => this.setState({ resultsList: data, loading: false }));
      }
    );
    this.props.displaySwitchOrNot(this.props.location.pathname);
  }

  componentDidUpdate(prevProps) {
    if (this.props.location.search !== prevProps.location.search) {
      this.setState({ loading: true }, () => {
        fetch(
          `/newsapi/v1.0/${
            this.state.source
          }/search/${this.props.location.search.substring(3)}`
        )
          .then(res => res.json())
          .then(data => this.setState({ resultsList: data, loading: false }));
      });
    }
  }

  render() {
    return (
      <>
        {this.state.loading ? (
          <Loading />
        ) : (
          <Container fluid>
            <Row className="h3 pl-3 mt-2">Results</Row>
            <Row>
              {this.state.resultsList.map((article, idx) => {
                return (
                  <Col key={idx} sm={3}>
                    <SmallArticalCard article={article} />
                  </Col>
                );
              })}
            </Row>
          </Container>
        )}
      </>
    );
  }
}

export default SearchResults;
